/* Navbar.js */
@media (max-width: 1024px) {
  .main-navbar {
    flex-wrap: wrap;
    padding: 12px 20px;
  }

  .menu {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;  
    gap: 10px;
    width: 100%;
  }

  .right-section {
    gap: 15px;
  }

  .call-box {
    padding: 8px 12px;
  }
}

@media (max-width: 768px) {
  .main-navbar {
    flex-direction: column;
    align-items: center;
    gap: 15px;
    padding: 12px 15px;
  }

  .logo-section img {
    max-height: 45px;
  }

  .menu {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;  
    gap: 10px;
    width: 100%;
  }

  .menu a {
    font-size: 14px;
  }

  .right-section {
    justify-content: center;
    width: 100%;
  }

  .call-box {
    margin-top: 8px;
  }
}

@media (max-width: 480px) {
  .main-navbar {
    flex-direction: column;
    align-items: center;
    gap: 12px;
    padding: 10px 12px;
  }

  .logo-section img {
    max-height: 38px;
  }

  .menu {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between; 
    gap: 5px;
    width: 100%;
  }

  .menu a {
    font-size: 13px;
  }

  .right-section {
    width: 100%;
    justify-content: center;
  }

  .call-box {
    flex-direction: column;
    align-items: center;
    gap: 5px;
    padding: 10px;
    border-radius: 10px;
    width: 100%;
  }

  .call-number {
    font-size: 12px;
  }
} 
/* end Navbar.js */

/* Bgimg.js */
html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

@media (max-width: 1024px) {
  .hero-container {
    height: 50dvh; 
    max-height: 50dvh;
    overflow: hidden;
  }

  .hero-bg {
    background-size: cover;
    background-position: center;
  }

  .hero-arrows {
    display: none;
  }
}

@media (max-width: 768px) {
  .hero-container {
    height: 50dvh; 
    max-height: 50dvh;
    overflow: hidden;
  }

  .hero-bg {
    background-size: cover;
    background-position: center;
  }

  .hero-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 90%;
    padding: 0 10px;
  }

  .hero-title {
    font-size: 26px;
    line-height: 1.3;
  }

  .hero-subtitle {
    font-size: 12px;
  }

  .hero-btn {
    font-size: 13px;
    padding: 10px 18px;
    
  }

  .hero-arrows {
    display: none;
  }
}

@media (max-width: 480px) {
  .hero-container {
    height: 50dvh;   
    max-height: 50dvh;
    overflow: hidden;
  }

  .hero-title {
    font-size: 28px;
  }

  .hero-subtitle {
    font-size: 15px;
  }

  .hero-btn {
    font-size: 15px;
    padding: 8px 14px;
  }

  .hero-arrows {
    display: none;
  }
}
/* end Bgimg.js */

