/* Solutionimage.js */
@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(108, 99, 255, 0.6);
  }
  50% {
    transform: scale(1.1);
    box-shadow: 0 0 0 15px rgba(108, 99, 255, 0);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(108, 99, 255, 0);
  }
}

@media (max-width: 992px) {
  .solutionimage-content {
    flex-direction: column;
    text-align: center;
    gap: 40px;
  }

  .solutionimage-left {
    max-width: 100%;
  }

  .solutionimage-right {
    flex-direction: row;
    justify-content: center;
    gap: 20px;
  }
}

@media (max-width: 600px) {
  .solutionimage-title {
    font-size: 28px;
  }
  .solutionimage-box {
    padding: 20px;
  }
}
/* end Solutionimage.js */

