/* Footer.js */
@media (max-width: 1024px) {
  .footer-container {
    grid-template-columns: repeat(2, 1fr);  
    gap: 30px;
    margin: 0 auto;
    padding: 0 20px;
  }

  .footer-col.newsletter,
  .footer-contact {
    margin-left: 0;  
  }
}

@media (max-width: 768px) {
  .footer-container {
    grid-template-columns: 1fr;  
    text-align: center;
    margin: 0 auto;
    padding: 0 15px;
  }

  .footer-col {
    margin-bottom: -24px;
  }

  .footer-logo {
    justify-content: center;
  }

  .footer-socials {
    justify-content: center;
    margin: 15px 0;
  }

  .footer-col h3::after {
    margin: 5px auto;  
  }

  .newsletter-form {
    margin: 10px auto 0;
    width: 100%;
    max-width: 350px;
  }

  .footer-col.newsletter,
  .footer-contact {
    margin-left: 0;  
  }

  .contact-item {
    justify-content: center;
  }

  .contact-item .icon {
    margin-right: 8px;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 40px 20px 15px;
  }

  .footer-col p,
  .footer-col ul li a,
  .contact-item span {
    font-size: 14px;
  }

  .footer-bottom {
    font-size: 12px;
    padding: 12px 0;
  }
}
/* end Footer.js */